/* ------------------------------------*\
    $BUTTONS
\*------------------------------------ */

.o-button,
button,
input[type="submit"],
a.fasc-button {
  cursor: pointer;
  box-shadow: none;
  transition: all 0.25s ease-in-out;
  position: relative;
  padding: $pad/2 $pad*2 $pad/2 $pad;
  margin: $space 0 0 0;
  border: 1px solid $gray-dark;
  display: table;
  vertical-align: middle;
  text-align: center;
  width: auto;
  background: $button-color;
  color: $button-hover;
  font-size: rem(18);

  @include media('>large') {
    padding: $pad/1.5 $pad*2 $pad/1.5 $pad;
  }

  &:focus {
    outline: 0;
  }

  &:hover {
    background-color: $button-hover;
    color: $white;
    border-color: $button-hover;

    &::after {
      background: url('../assets/images/icon--arrow--white.svg') center center no-repeat;
      background-size: rem(15);
    }
  }

  &::after {
    content: '';
    display: block;
    margin-left: $space-half;
    background: url('../assets/images/icon--arrow.svg') center center no-repeat;
    background-size: rem(15);
    width: rem(20);
    height: rem(20);
    position: absolute;
    right: $space-half;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.25s ease-in-out;
  }
}

.u-button--white {
  color: $white;
  background-color: transparent;

  &:hover {
    background-color: $white;
    color: $black;

    &::after {
      background: url('../assets/images/icon--arrow.svg') center center no-repeat;
      background-size: rem(15);
    }
  }
}

a.fasc-button {
  background: $button-color !important;
  color: $button-hover !important;

  &:hover {
    background-color: $button-hover !important;
    color: $white !important;
    border-color: $button-hover;
  }
}



